<template lang="html">
  <div class="content-wrapper">
    <!-- Main content -->
    <section class="content">
      <div class="row">
        <div class="col-xs-12">
          <div class="box">
            <div class="box-header">
              <h3 class="box-title"></h3>
              <el-button type="primary" class="pull-right" size="small" @click="isMask=true"></el-button>
            </div>
            <!-- /.box-header -->
            <div class="box-body screen-top">
              <div class="screen-item">
                <span class="screen-title"></span>
                <el-input v-model="query.businessOrderNo" placeholder="" size="small"
                          style="width:220px"></el-input>
              </div>
              <div class="screen-item">
                <span class="screen-title"></span>
                <el-input v-model="query.orderNo" placeholder="" size="small" style="width:220px"></el-input>
              </div>
              <div class="screen-item">
                <span class="screen-title"></span>
                <el-input v-model="query.sn" placeholder="" size="small" style="width:220px"></el-input>
              </div>
              <div class="screen-item">
                <span class="screen-title"></span>
                <el-input v-model="query.merchantName" placeholder="" size="small" style="width:220px"></el-input>
              </div>
              <div class="screen-item">
                <span class="screen-title"></span>
                <el-select v-model="query.appId" size="small" placeholder="" style="width: 220px">
                  <el-option v-for="item in item_appId"
                             :label="item.label"
                             :value="item.value">
                  </el-option>
                </el-select>
              </div>
              <div class="screen-item">
                <span class="screen-title"></span>
                <el-select v-model="query.status" size="small" clearable placeholder="" style="width: 220px">
                  <el-option v-for="item in item_status"
                             :label="item.label"
                             :value="item.value">
                  </el-option>
                </el-select>
              </div>
              <div class="screen-item">
                <span class="screen-title"></span>
                <el-select v-model="query.payType" size="small" clearable placeholder="" style="width: 220px">
                  <el-option v-for="item in item_payType"
                             :label="item.label"
                             :value="item.value">
                  </el-option>
                </el-select>
              </div>
              <div class="screen-item">
                <span class="screen-title"></span>
                <el-select v-model="query.settleStatus" size="small" clearable placeholder="" style="width: 220px">
                  <el-option v-for="item in item_settleStatus"
                             :label="item.label"
                             :value="item.value">
                  </el-option>
                </el-select>
              </div>
              <div class="screen-item">
                <span class="screen-title"></span>
                <el-date-picker size="small"
                                v-model="date"
                                type="daterange"
                                align="right"
                                @change="datetimeSelect"
                                placeholder=""
                                :picker-options="pickerOptions2">
                </el-date-picker>
              </div>
              <div class="screen-item">
                <span class="screen-title"></span>
                <el-input v-model="query.proxyName" placeholder="" size="small" style="width:220px"></el-input>
              </div>
              <div class="screen-item">
                <span class="screen-title"></span>
                <el-button type="primary" size="small" @click="screen"></el-button>
              </div>
            </div>
            <div class="box-body" style="padding-bottom: 0">
              <span><b></b> {{allTotal | fix}}  {{pageTotal | fix}}</span>
              <span style="margin-left: 20px"><b></b> {{allTotal1 | fix}}  {{pageTotal1 | fix}}</span>
            </div>
            <div class="box-body">
              <el-table v-loading.body="tableLoading" style="font-size: 12px;margin:15px 0" :data="records" border>
                <el-table-column type="index" width="62" label="" fixed="left"></el-table-column>
                <el-table-column prop="appId" label="" min-width="85"></el-table-column>
                <el-table-column label="" min-width="112">
                  <template scope="scope">
                    <span class="td" :data-clipboard-text="records[scope.$index].orderNo" type="text" size="small"
                          style="cursor: pointer" title="">{{records[scope.$index].orderNo|changeHide}}</span>
                  </template>
                </el-table-column>
                <el-table-column prop="merchantName" label="" min-width="120"></el-table-column>
                <el-table-column prop="proxyName" label="" min-width="90"></el-table-column>
                <el-table-column prop="proxyName1" label="" min-width="110"></el-table-column>
                <el-table-column label="" min-width="112">
                  <template scope="scope">
                    <span class="td" :data-clipboard-text="records[scope.$index].businessOrderNo" type="text"
                          size="small" style="cursor: pointer" title="">{{records[scope.$index].businessOrderNo|changeHide}}</span>
                  </template>
                </el-table-column>
                <el-table-column label="" min-width="90" align="right">
                  <template scope="scope">
                    {{scope.row.tradeAmount | filter_toFix}}
                  </template>
                </el-table-column>
                <el-table-column label="" min-width="80" align="right">
                  <template scope="scope">
                    {{scope.row.poundage | filter_toFix}}
                  </template>
                </el-table-column>
                <el-table-column label="" min-width="90">
                  <template scope="scope">
                    {{scope.row.statusValue}}
                  </template>
                </el-table-column>
                <el-table-column label="" min-width="112">
                  <template scope="scope">
                    <span class="td" :data-clipboard-text="records[scope.$index].sn" type="text" size="small"
                          style="cursor: pointer" title="">{{scope.row.sn|changeHide}}</span>
                  </template>
                </el-table-column>
                <el-table-column prop="payType" label="" min-width="115"></el-table-column>
                <el-table-column prop="settleStat" label="" min-width="90"></el-table-column>
                <el-table-column prop="createTimed" label="" width="162"></el-table-column>
                <el-table-column prop="paySuccessTimes" label="" width="162"></el-table-column>
                <el-table-column prop="settleType" label=""></el-table-column>
              </el-table>
            </div>
            <div class="box-body">
              <el-pagination style="float:right"
                             @size-change="handleSizeChange"
                             @current-change="handleCurrentChange"
                             :current-page="query.page"
                             :page-sizes="[20, 100, 200, 500]"
                             :page-size="query.size"
                             layout="total, sizes, prev, pager, next, jumper"
                             :total="total">
              </el-pagination>
            </div>
            <div class="box box-info mask el-message-box" v-if="isMask">
              <div class="maskCon">
                <div class="head">
                  <div class="title"></div>
                  <i class="el-icon-close" @click="isMask=false"></i>
                </div>
                <div class="body">
                  <div></div>
                </div>
                <div class="foot">
                  <a href="javascript:void(0)" @click="isMask=false" class="el-button el-button--default"></a>
                  <a :href="'http://'+loadUrl" @click="isMask=false"
                     class="el-button el-button-default el-button--primary "></a>
                </div>
              </div>
            </div>
            <!-- /.box-body -->
          </div>
          <!-- /.box -->
        </div>
        <!-- /.col -->
      </div>
      <!-- /.row -->
    </section>
  </div>
</template>
<script lang="babel">
  import store from '../store'
  import Clipboard from "clipboard"
  export default {
    data () {
      return {
        isMask: false,
        loadUrl:'',
        query: {
          page: 1,
          size: 20,
          orderNo: '',
          businessOrderNo: '',
          sn: '',
          merchantName: '',
          merchantNo: '',
          startTime: '',
          endTime: '',
          lessTotalFee: '',
          moreTotalFee: '',
          appId: 'hss',
          status: '',
          settleStatus: '',
          payType: '',
          proxyName: '',
        },
        records: [],
        item_appId: [
          {value: 'hss', label: ''},
          {value: 'hsy', label: ''},
        ],
        item_status: [
          {value: '', label: ''},
          {value: '1', label: ''},
          {value: '4', label: ''},
          {value: '3', label: ''}
        ],
        item_settleStatus: [
          {value: '', label: ''},
          {value: '1', label: ''},
          {value: '2', label: ''},
          {value: '3', label: ''}
        ],
        item_payType: [
          {value: '', label: ''},
          {value: 'sm_wechat_jsapi', label: ''},
          {value: 'sm_alipay_jsapi', label: ''},
          {value: 'sm_wechat_code', label: ''},
          {value: 'sm_alipay_code', label: ''},
          {value: 'sm_unionpay', label: ''},
          {value: 'km_wechat_jsapi', label: ''},
          {value: 'km_alipay_jsapi', label: ''},
          {value: 'km_wechat_code', label: ''},
          {value: 'km_alipay_code', label: ''},
          {value: 'mb_unionpay', label: ''},
          {value: 'hzyb_wechat', label: ''},
          {value: 'hzyb_alipay', label: ''},
          {value: 'yijia_wechat', label: '+'},
          {value: 'yijia_alipay', label: '+'},
        ],
        date: '',
        total: 0,
        pageTotal: 0,
        pageTotal1: 0,
        allTotal: 0,
        allTotal1: 0
      }
    },
    created(){
      var clipboard = new Clipboard('.td');
      // 
      clipboard.on('success', (e) => {
        this.$message({
          showClose: true,
          message: "  " + e.text,
          type: 'success'
        });
      });
      let time = new Date();
      this.date = [time, time];
      for (var j = 0; j < this.date.length; j++) {
        var str = this.date[j];
        var ary = [str.getFullYear(), str.getMonth() + 1, str.getDate()];
        for (var i = 0, len = ary.length; i < len; i++) {
          if (ary[i] < 10) {
            ary[i] = '0' + ary[i];
          }
        }
        str = ary[0] + '-' + ary[1] + '-' + ary[2];
        if (j == 0) {
          this.query.startTime = str;
        } else {
          this.query.endTime = str;
        }
      }
      this.getData();
      this.getTotal();
    },
    methods: {
      datetimeSelect: function (val) {
        if (val == undefined) {
          this.query.startTime = '';
          this.query.endTime = '';
        } else {
          let format = val.split(' - ');
          this.query.startTime = format[0];
          this.query.endTime = format[1];
        }
      },
      screen: function () {
        this.query.page = 1;
        this.getData();
        this.getTotal();
      },
      getTotal: function () {
        this.$http.post('/daili/tradeQuery/amountCount', this.query).then(res => {
          this.allTotal = res.data.totalPayment==null?0:res.data.totalPayment;
          this.allTotal1 = res.data.totalPoundage==null?0:res.data.totalPoundage;
        }, err => {
          console.log(err);
        })
      },
      getData: function () {
        this.tableLoading = true;
        this.$http.post('/daili/tradeQuery/tradeList', this.query).then(res => {
          this.tableLoading = false;
          this.total = res.data.count;
          this.records = res.data.records;
          this.loadUrl = res.data.ext;
          let t = 0,s = 0;
          for (let i = 0; i < res.data.records.length; i++) {
            t += res.data.records[i].tradeAmount / 1;
            s += res.data.records[i].poundage / 1;
          }
          this.pageTotal = t;
          this.pageTotal1 = s;
        }, err => {
          this.tableLoading = false;
          this.$message({
            showClose: true,
            message: err.data.msg,
            type: 'error'
          });
        });
      },
      handleSizeChange(val) {
        this.query.page = 1;
        this.query.size = val;
        this.getData();
      },
      handleCurrentChange(val) {
        this.query.page = val;
        this.getData();
      }
    },
    filters: {
      changeHide: function (val) {
        if (val != "" && val != null) {
          val = val.replace(val.substring(3, val.length - 6), "");
        }
        return val
      }
    }
  }
</script>
<style scoped lang="less">
  .screen-top {
    padding-top: 0 !important;
  }

  .screen-item {
    float: left;
    margin-right: 10px;
  }

  .screen-title {
    display: block;
    height: 24px;
    line-height: 24px;
  }
  .mask {
    z-index: 2020;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.45);

  .maskCon {
    margin: 250px auto;
    text-align: left;
    vertical-align: middle;
    background-color: #fff;
    width: 420px;
    border-radius: 3px;
    font-size: 16px;
    overflow: hidden;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;

  .head {
    position: relative;
    padding: 20px 20px 0;

  .title {
    padding-left: 0;
    margin-bottom: 0;
    font-size: 16px;
    font-weight: 700;
    height: 18px;
    color: #333;
  }

  i {
    font-family: element-icons !important;
    speak: none;
    font-style: normal;
    font-weight: 400;
    font-variant: normal;
    text-transform: none;
    vertical-align: baseline;
    display: inline-block;
    -webkit-font-smoothing: antialiased;
    position: absolute;
    top: 19px;
    right: 20px;
    color: #999;
    cursor: pointer;
    line-height: 20px;
    text-align: center;
  }

  }
  .body {
    padding: 30px 20px;
    color: #48576a;
    font-size: 14px;
    position: relative;

  div {
    margin: 0;
    line-height: 1.4;
    font-size: 14px;
    color: #48576a;
    font-weight: 400;
  }
  }
  .foot {
    padding: 10px 20px 15px;
    text-align: right;
  }
  }
  }
</style>



